@import "../../css/colors";

.navbar {
    padding: 0;
    margin-bottom: 15px;
}

.nav-link {
    text-transform: uppercase;
    padding: 12px;
}

.dropdown-item {
    text-transform: uppercase;
}

.active {
    color: $red !important;
}

@media only screen and (min-width: 320px) {
    .navbar {
        border-left: none;
    }

    .navbar-toggler {
        margin-top: 15px;
    }
}

@media (min-width: 992px) {
    .navbar {
        border-left: 1px solid $red;
    }
}
