@import "../../css/colors";

.footer {
    margin-top: 20px;
    text-align: center;
}

.social-media li {
    display: inline;
    font-size: 25px;
}

.fa-facebook {
    color: $facebook;
}

.fa-instagram {
    color: $instagram;
}

.fa-twitter {
    color: $twitter;
}

.fa-youtube {
    color: $youtube;
}

@media only screen and (min-width: 320px) {
    .footer-contact {
        font-size: 11px;
    }

    .social-media li {
        padding: 15px;
    }
}
@media (min-width: 992px) {
    .footer-contact {
        font-size: 20px;
    }

    .social-media li {
        padding: 20px;
    }
}
