@import "colors";

/* Google Fonts
--------------------------------------------------------------------------------*/
@import url(https://fonts.googleapis.com/css?family=Manrope);

/* Default Element Styles
--------------------------------------------------------------------------------*/
body {
    margin: 0;
    background-color: $secondaryDark; 
    font-family: 'Manrope';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
     monospace;
}

ul {
    padding: 0;
}

/* Layout
--------------------------------------------------------------------------------*/
.gallery img {
    width: 100%;
    padding: 10px;
}

.about {
    text-align: left;
}

.about p {
    text-indent: 40px;
}

.about-hero {
    width: 100%;
    margin-bottom: 20px;
}

/* Breakpoint Styles
--------------------------------------------------------------------------------*/
@media only screen and (max-width: 600px) {
    .about p {
        max-width: 350px;
        margin: 0 auto;
    }
    
    .gallery p {
        font-size: 12px;
    }
}
