@import "../../css/colors";

.main-head {
    background-color: $mainDark;
    min-height: 50px;

    .container {
        display: flex;
    }

    h1 {
        margin: 0;
        color: $white;

        .last-name {
            color: $highlight;
        }
    }
    em {
        color: $white;
        margin-top: auto;
    }
}

@media only screen and (min-width: 320px) {
    .main-head {
        text-align: center;

        .container {
            flex-direction: column;
        }
        
        h1 {
            font-size: 20px;
            padding: 20px 0 0 0;
        }
        
        em {
            font-size: 14px;
            padding: 0 0 20px 0;
        }
    }
}

@media (min-width: 992px) {
    .main-head {
        .container {
            flex-direction: row;
        }

        h1 {
            font-size: 28px;
            padding: 20px 0;
        }
        em {
            font-size: 18px;
            padding: 20px;
        }
    }
}
